.text-border(@bordersize : 2px, @bordercolor : #fff) {
	
	text-shadow: @bordersize 0 0 @bordercolor, -@bordersize 0 0 @bordercolor, 0 @bordersize 0 @bordercolor, 0 -@bordersize 0 @bordercolor, @bordersize @bordersize @bordercolor, -@bordersize -@bordersize 0 @bordercolor, @bordersize -@bordersize 0 @bordercolor, -@bordersize @bordersize 0 @bordercolor;
	
}




