#wrapper {
  width: 100%;
  max-width: 1000px;
  background: #fff;
  position: relative;
  margin: auto;
  box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.3);
}
body {
  background: rgba(146, 96, 169, 0.25);
}
.single-avada_portfolio .flexslider .slides {
  margin-top: 10px;
  height: 450px;
  overflow: hidden;
  position: relative;
}
.single-avada_portfolio .flexslider .slides li {
  position: absolute !important;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
}
body {
  font-size: 14px;
}
a:link,
a:hover,
a:active,
a:visited {
  color: #9260a9;
}
nav#nav,
nav#sticky-nav {
  clear: both !important;
  float: none;
  width: 100%;
  margin: auto;
}
nav#nav ul,
nav#sticky-nav ul {
  text-align: center;
}
nav#nav ul li,
nav#sticky-nav ul li {
  display: inline-block;
  float: none;
  padding: 0 17px;
}
body .ui-tooltip {
  background: #fff;
}
.ampersand-group-menu {
  display: block;
  float: right;
}
.ampersand-group-menu ul {
  border-left: 1px solid #b2b2b2;
  width: 300px;
  list-style-type: none;
  padding: 0 0 0 10px;
  margin: 0;
  font-family: Arial;
}
.ampersand-group-menu ul li {
  font-size: 12px;
  float: none;
  margin: 0;
  padding: 0;
}
.ampersand-group-menu ul li a {
  color: #7b4977;
  height: auto;
  line-height: 14px;
}
@media screen and (max-width: 800px) {
  .ampersand-group-menu {
    float: none;
  }
  .ampersand-group-menu ul {
    width: 100%;
    border-left: none;
    text-align: center;
  }
  .ampersand-group-menu ul li {
    display: inline-block;
    margin: 10px;
  }
}
a .aquote {
  transition: transform 0.5s ease;
}
a .aquote:hover {
  transform: scale(1.2, 1.2);
}
.aquote {
  font-size: 22px;
  display: inline-block;
  line-height: 34px;
  color: #808080;
  font-family: "Avenir LT W01 65 Medium";
}
.aquote strong {
  font-family: "BaskervilleHandcutW01-R 927206";
  font-style: normal !important;
  font-weight: normal;
}
.aquote.pink:after,
.aquote.pink strong {
  color: #f9cbd6;
}
.aquote.babyblue:after,
.aquote.babyblue strong {
  color: #cce8fe;
}
.aquote.purple:after,
.aquote.purple strong {
  color: #9260a9;
}
.aquote.peach:after,
.aquote.peach strong {
  color: #e3c7ac;
}
.aquote:after {
  font-family: 'icomoon';
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: smaller;
  padding-left: 10px;
}
.aquote.long-right:after {
  content: "\e600";
}
.aquote.long-left:after {
  content: "\e605";
}
.aquote.round-right:after {
  content: "\e604";
}
.aquote.round-left:after {
  content: "\e602";
}
.aquote.floral-left:after {
  content: "\e606";
  margin-left: 4px;
}
.aquote.floral-right:after {
  content: "\e601";
}
.aquote.heart:after {
  content: "\e603";
}
.aquote.grey {
  color: #9260a9;
}
.aquote.grey strong {
  color: #b2b2b2;
}
.aquote.grey:after {
  color: #b2b2b2;
}
body.home div.post-content div.two_third:nth-child(1) {
  position: relative;
}
body.home div.post-content div.two_third:nth-child(1) span.aquote {
  background: #fff;
  position: relative;
  z-index: 1;
  padding-right: 10px;
}
body.home div.post-content div.two_third:nth-child(1):after {
  content: "";
  height: 1px;
  background: #b2b2b2;
  position: absolute;
  width: 100%;
  top: 0;
  margin-top: 7px;
  bottom: 0;
  left: 0;
}
span.rightline {
  float: right;
  height: 1px;
  background: #b2b2b2;
  display: block;
  top: 7px;
  position: relative;
}
.category-section-menu ul {
  margin: 0;
  padding: 0;
}
.category-section-menu ul li {
  display: block;
}
@media only screen and (max-width: 550px) {
  body.home div.two_third,
  body.home div.one_third {
    width: 100%;
    float: none;
    clear: both;
  }
  span.rightline {
    display: none;
  }
  .category-section-menu ul {
    width: 100%;
    text-align: center;
  }
  .category-section-menu ul li {
    display: inline-block;
    width: auto;
